.container{
    position: absolute;
    left: 40%;
    bottom: 5px;
    box-shadow: 0px 12px 25px #c4c4c4;
    border-radius: 15px;
    /*border: 1px solid black;*/
}

.sidebar{
    display: inline-flex;
    position: relative;
    bottom: 0px;
    border-radius: 15px;
    /*border: 1px solid red;*/
}
.sidebar-container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    box-sizing: border-box;
    background-color:rgb(255, 255, 255);
    border: solid 1px rgb(161, 161, 161);
    border-radius: 15px;
    /* border: 1px solid black; */
}

.sidebar-item{
    box-sizing: content-box;
    border-radius: 15px;
    width: 45px;
    /*border: 1px solid blue;*/
}
